.header {
	width: 100%;
	background-color: rgb(212, 201, 188);
	position: fixed;
	top: 0;
	left: 0;
	z-index: 11;
	transition: 1s;


}
.header__content {
	height: 90px;
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 0 15px;
}

.header__search {
	max-width: 700px;
	width: 100%;

	display: flex;
}
.header-dark {
	background-color: rgb(43, 41, 41);
}

.search__bar {
	width: 100%;
	padding: 5px 10px;
}

.search__btn {
	padding: 5px;
	transition: 1s;
	cursor: pointer;
}

.search__btn:hover {
	background-color: rgb(90, 90, 190);
	color: #fff;
}

.redirect__btn {
	cursor: pointer;

	padding: 7px;
	margin-left: 10px;

	transition: 1s;
}

.redirect__btn:hover {
	background-color: rgb(90, 90, 190);
	color: #fff;
}
